@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2");
  src: url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2");
  src: url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/lato-regular.woff2") format("woff2");
  src: url("../fonts/lato/lato-regular.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.main-header {
  background-color: #ffffff;
}
@media (min-width: 1440px) {
  .main-header {
    background-color: transparent;
  }
}
.main-header__wrapper--form {
  border-bottom: 1px solid #d9d9d9;
}
@media (min-width: 768px) {
  .main-header__wrapper {
    display: flex;
    align-items: center;
    padding-top: 25px;
    justify-content: space-between;
    width: calc(100% - 60px);
    margin: 0 auto;
  }
  .main-header__wrapper--form {
    border-bottom: none;
  }
}
@media (min-width: 1440px) {
  .main-header__wrapper {
    width: calc(100% - 220px);
    max-width: 1220px;
    margin-bottom: -120px;
    padding: 0;
    padding-top: 54px;
  }
  .main-header__wrapper--catalog {
    padding-top: 55px;
    margin-bottom: 9px;
  }
  .main-header__wrapper--form {
    padding-top: 55px;
    margin-bottom: 9px;
    border-bottom: none;
    max-width: 100%;
  }
}
.main-header__wrapper-logo {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: calc(100% - 40px);
  margin: 0 auto;
  padding-top: 13px;
  padding-bottom: 11px;
}
@media (min-width: 768px) {
  .main-header__wrapper-logo {
    justify-content: start;
    width: auto;
    margin: 0;
    padding: 0;
  }
}
.main-header__toggle {
  padding: 4px 0;
  width: 24px;
  height: 22px;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
}
