  font-family: "Lato", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}
.main-nav {
  position: relative;
}
.main-nav--closed {
  display: none;
}
.main-nav--opened {
  display: static;
}
.main-nav__menu-list {
  position: absolute;
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  list-style-type: none;
}
