    padding: 0;
    border: none;
  }
}
.main-nav__link {
  position: relative;
  font-size: 20px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
  font-family: "Oswald", sans-serif;
  color: #000000;
  text-decoration: none;
}
.main-nav__link:hover {
  opacity: 0.6;
}
.main-nav__link:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
