    font-size: 18px;
    line-height: 24px;
    padding-bottom: 10px;
  }
  .main-nav__link--active::after {
    position: absolute;
    content: "";
    left: 0;
    width: 100%;
    height: 2px;
    top: 33px;
    background-color: #68b738;
  }
}
