    font-size: 20px;
    font-weight: 400;
    line-height: normal;
  }
  .main-nav__link--active::after {
    position: absolute;
    content: "";
    left: 0;
    width: 100%;
    height: 2px;
    top: 36px;
    background-color: #ffffff;
  }
}
@media (min-width: 768px) {
  .main-nav__link--catalog-active::after {
    position: absolute;
    content: "";
    left: 0;
    width: 100%;
    height: 2px;
    top: 30px;
    background-color: #68b738;
  }
}
@media (min-width: 1440px) {
  .main-nav__link--catalog {
    color: #000000;
  }
  .main-nav__link--catalog-active::after {
    top: 35px;
  }
}
@media (min-width: 768px) {
  .main-nav__link--form-active::after {
    position: absolute;
    content: "";
    left: 0;
    width: 100%;
    height: 2px;
    top: 30px;
    background-color: #68b738;
  }
}
@media (min-width: 1440px) {
  .main-nav__link--form {
    color: #000000;
  }
  .main-nav__link--form-active::after {
    top: 35px;
  }
}
.hero {
