}
.product-show {
  position: relative;
}
.product-show::before {
  position: absolute;
  content: "";
  left: 0;
  top: 85px;
  width: 100%;
  height: calc(100% - 80px);
}
@media (min-width: 768px) {
  .product-show::before {
    border: 1px solid #f2f2f2;
  }
}
@media (min-width: 1440px) {
  .product-show::before {
    top: 75px;
    height: calc(100% - 75px);
  }
}
.product-show_wrapper {
  display: grid;
  width: calc(100% - 40px);
  height: 100%;
  margin: 0 auto;
  padding-top: 62px;
  background-image: url("../../img/plus.svg");
  background-repeat: no-repeat;
  background-position-x: center;
  background-position-y: 10px;
  text-align: center;
}
@media (min-width: 768px) {
  .product-show_wrapper {
    position: relative;
    width: 100%;
    grid-template-columns: 1fr;
    grid-template-rows: 246px 1fr;
    padding-top: 0;
    padding-bottom: 0;
    background-size: 60px 60px;
    background-position-y: 33%;
  }
}
@media (min-width: 1440px) {
  .product-show_wrapper {
    grid-template-rows: 224px 1fr min-content min-content;
    background-position-y: 31%;
  }
}
.product-show__title {
  margin: 0;
  margin-bottom: 10px;
  color: #222222;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .product-show__title {
    grid-row: 2/3;
    width: 145px;
    min-height: 48px;
    margin: 0 auto;
    margin-bottom: 5px;
    padding: 0;
    font-size: 20px;
    line-height: 24px;
  }
}
.product-show__description {
  margin: 0;
  margin-bottom: 21px;
  padding: 0;
  color: #444444;
  font-size: 12px;
  line-height: 16px;
  font-family: "Lato", "Arial", sans-serif;
}
@media (min-width: 768px) {
  .product-show__description {
    grid-row: 3/4;
    min-height: 36px;
    font-size: 16px;
    line-height: 18px;
    margin-bottom: 70px;
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media (min-width: 1440px) {
  .product-show__description {
    padding-left: 1px;
    padding-right: 1px;
  }
}
@media (min-width: 768px) {
  .product-show__button {
    grid-row: 4/5;
    padding-left: 10px;
    padding-right: 10px;
  }
}
